import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { InvoicesComponent } from './invoices.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { EditComponent } from './edit/edit.component';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { BillType15Module } from 'src/app/sales/components/bill-type/bill-type-15/bill-type-15.module';
const routes: Routes = [
  {
    path: '',
    component: InvoicesComponent
  },
  {
    path: 'add',
    component: AddComponent
  },
  {
    path: 'detail',
    component: DetailComponent
  },
  {
    path: 'edit',
    component: EditComponent
  }
]

@NgModule({
  declarations: [InvoicesComponent, EditComponent, AddComponent, DetailComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    BillType15Module
  ]
})
export class InvoicesModule { }
