import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhSaleInvoice } from 'src/app/sales/interface/vh-sale-invoice';
import { VhWarrantyInvoice } from 'src/app/sales/interface/vh-warranty-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-invoices',
  templateUrl: './invoices.component.html',
  styleUrls: ['./invoices.component.scss']
})
export class InvoicesComponent implements OnInit {

  public searchValue: string = ''
  public invoiceShow: any[] = []
  public invoiceToday: VhWarrantyInvoice[];
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  startTime: Date = new Date()
  endTime: Date = new Date()
  public filterType: 1 | 2 | 3 | 4 = 1;
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhAuth: VhAuth,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private cdRef: ChangeDetectorRef,
  ) {
    this.startTime.setHours(0, 0, 0, 0)
    this.endTime.setHours(23, 59, 59, 59)
  }
  listFilter: Array<any> = [{ value: 1, name: "Latest" }, { value: 2, name: "Oldest" }, { value: 3, name: "Low to High value" }, { value: 4, name: "High to Low value" }]
  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
    }
    else { this.filterType = 1; }
    if (!this.invoiceToday) this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoiceToday(this.filterType))
    else this.getInvoiceToday(this.filterType)
  }
  /**
   * get danh sách hóa đơn bảo hành
   * @param filterType 
   */
  getInvoiceToday(filterType?) {
    this.vhQuerySales.getBills_byFields( { date: { $gte: this.startTime, $lte: this.endTime }, bill_type: { $in: [15, 16, 17] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {})
      .then((invoice: VhWarrantyInvoice[]) => {
        if (!this.invoiceToday) this.vhComponent.hideLoading(0)
        this.invoiceToday = invoice.map(item => {
          return {
            ...item,
            partner_name: this.renderPartner(item.id_customer),
            payment_name: this.renderPayment(item.payment_type, item.id_wallet),
            status: this.renderStatus(item.bill_type),
            technician_name: this.renderEmployee(item.id_technician),
            reception_name: this.renderEmployee(item.id_employee),
            total: item.total * (1 + item.tax / 100)
          }
        })
        if (filterType) this.sortFilter(filterType)
        else this.searchBill(this.searchValue)
      })
  }
  /**
   * trả về tên trạng thái bill theo bill_type
   * @param bill_type 
   * @returns string
   * @example let status  = this.renderStatus(15)
   */
  renderStatus(bill_type: 15 | 16 | 17) {
    switch (bill_type) {
      case 16: return this.languageService.translate('Temporary')
      case 17: return this.languageService.translate('Cancelled')
      default: return this.languageService.translate('Issued')
    }
  }
  /**
   * trả về tên khách hàng của bill
   * @param id_partner
   * @returns string
   * @example let customer_name = this.renderPartner("DHWIUGDHUWYGHDWOUIDH")
   */
  renderPartner(id_partner: string) {
    let customer: string = this.languageService.translate(this.vhQuerySales.getlocalCustomer(id_partner).name)

    return customer ? customer : this.languageService.translate("Not found")
  }
  /**
   * trả về tên nv của bill
   * @param id_partner
   * @returns string
   * @example let empl_name = this.renderEmployee("DHWIUGDHUWYGHDWOUIDH")
   */
  renderEmployee(id_employee: string) {
    let employee: string = this.languageService.translate(this.vhAuth.getlocalEmployee(id_employee).name)

    return employee ? employee : this.languageService.translate("Not found")
  }
  /**
   * trả về tên phương thức thanh toán của bill
   * @param id_partner
   * @returns string
   * @example let payment_method = this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.languageService.translate('Cash')
      case 2: return this.languageService.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }
  /**
   * lọc bill theo ['bill_code', 'partner_name', 'employee_name', 'payment_name', 'status', 'phone']
   * @param value 
   */
  searchBill(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceToday, ['bill_code', 'partner_name', 'employee_name', 'payment_name', 'status', 'phone'])
    } else this.invoiceShow = [...this.invoiceToday]
  }
  /**
   * sắp xếp bill theo filterType
   * @param filterType 
   */
  sortFilter(filterType: 1 | 2 | 3 | 4) {
    switch (filterType) {
      case 1: this.invoiceToday.sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date));   break;
      case 2: this.invoiceToday.sort((prev, next) => Date.parse(prev.date) - Date.parse(next.date));   break;
      case 3: this.invoiceToday.sort((prev, next) => (prev.total * (1 + prev.tax / 100)) - (next.total * (1 + next.tax / 100))); break;
      case 4: this.invoiceToday.sort((prev, next) => (next.total * (1 + next.tax / 100)) - (prev.total * (1 + prev.tax / 100)));   break;
    }
    this.searchBill(this.searchValue)
  }
  /* -----Pagination----- */
  onCurrentPageDataChange(value) {
  }

  goToDetail(id) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    if (this.vhAuth.checkMyPermission("warranty_enable_view_bill_today")) this.router.navigate(["/sales/dashboard/manage/warranty/invoices/detail"], { state: { id: id, dataRestore } })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  tableHeight: any;
  ngAfterViewChecked() {
    if (document.querySelector("#warranty-invoice-today") && document.querySelector(".ant-table-thead") && document.querySelector(".warranty-invoice-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#warranty-invoice-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".warranty-invoice-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }
  goToCart() {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    if (this.vhAuth.checkMyPermission("warranty_enable_create_bill")) this.router.navigate(["/sales/dashboard/manage/warranty/invoices/add"], { state: { dataRestore } })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/warranty"])
  }

  /** hàm sort cho các cột */
  dateCol = false;
  bill_codeCol = false;
  totalCol =  false;
  partner_nameCol = false;
  reception_nameCol = false;
  technician_nameCol = false;
  statusCol = false;
  /** Hàm thực hiện sắp xếp theo collection
   *
   * @param colName       // tên cột muôn sắp xếp
   */
  sortTable(colName){
    switch (colName) {
      case 'date':
        if(this.dateCol){
          this.invoiceShow = this.vhAlgorithm.sortDatebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortDatebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'bill_code':
        if(this.bill_codeCol){
          this.invoiceShow = this.vhAlgorithm.sortStringbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortStringbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'total':
        if(this.totalCol){
          this.invoiceShow = this.vhAlgorithm.sortNumberbyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortNumberbyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'partner_name':
        if(this.partner_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'reception_name':
        if(this.reception_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'technician_name':
        if(this.technician_nameCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
      case 'status':
        if(this.statusCol){
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyASC([...this.invoiceShow], colName);
        }else{
          this.invoiceShow = this.vhAlgorithm.sortVietnamesebyDESC([...this.invoiceShow], colName);
        }
        break;
    }
  }

}
